	export default {
		props: {
			rewardList: {
				type: Array,
				default () {
					return [];
				}
			},
			accountVO:{
				type: Object,
				default () {
					return {};
				}
			},
			userCode: {
				type: String,
				default () {
					return '';
				}
			},
			showReward: {
				type: Boolean,
				default () {
					return false;
				}
			}
		},
		data() {
			return {
				modalBar: [{
					name: '礼物',
					active: true,
					type:2
				}, {
					name: '我的道具',
					active: false,
					type:4
				}],
				checked: true,
				usename: 'use',
				listVO:{},
			};
		},
		methods: {
			hideModal() {
				this.$emit('hideModal')
				this.modalBar[0].active=true
				this.modalBar[1].active=false
			},
			clickTap(ind) {
				let modalBar = this.modalBar
				let self = this
				modalBar.filter((v, i) => {
					if (i == ind) {
						v.active = true
						self.$emit('getRewardList',v.type);
					} else {
						v.active = false
					}
				})
				this.modalBar = modalBar
			},
			checkChange(e) {
				let val = e.detail.value
				this.checked = !this.checked
			},
			chooseItem(index) {
				// debugger
				// this.dataList[index].choose=!this.dataList[index].choose
				// if(this.dataList[index].choose){
				this.listVO = this.rewardList[index]
				// }
				this.$parent.chooseReward(index)
				//this.$emit('chooseReward',index)
			},
			sureRward() {
                	let self = this
                	let params = {
                		buyNum : 1,
                		userCode : this.userCode,
                		virtualId : this.listVO.id,
                		sceneType: 2 
                	}
                	this.$acFrame.HttpService.virtualBuy(params).then(res => {
                		if (res.success) {
							self.hideModal()
							let orderVO=res.data
							getApp().globalData.orderType = 'reward'
							uni.navigateTo({
								url:'/pages/myshop/payWay?order='+JSON.stringify(orderVO)
							})
                		}
                	})
			},
			rewardUser(id, useType) {
				this.$emit('rewardUser', id, useType)
			},
		}
	}
